@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
:root{
  --primary-white: #ffff;
  --primary-blue: rgb(32, 97, 216);
  --primary-dark: rgba(255,255,255,0);
  --primary-orange: rgba(	255,140,0);
  --primary-red: rgba(128,0,0);
  --primary-hover-red: #f25050;
}

nav{
  z-index: 2;
}

.my-btn{
  border: .1rem solid rgba(	255,140,0);
  border: .1rem solid var(--primary-orange);
  background-color: transparent;
  padding: .5rem;
  border-radius: 1rem;
  margin: 0 .5rem;

}

nav a.nav-link{
  font-size: 0.8rem;

}

.navbar-nav > li a.active, .navbar-nav > li > a.focus{
  border-top: 0.1875rem solid rgba(	255,140,0);
  border-top: 0.1875rem solid var(--primary-orange)
}

.navbar-nav >li > a:hover{
  border-top: 0.1875rem solid rgba(	255,140,0);
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav >li > a{
  color: #ffff !important ;
  color: var(--primary-white) !important ;
  font-weight: bolder;
  font-size: 0.8rem;
  letter-spacing: 0.1rem;
  margin: 0 1rem;

  border-top: 0.1875rem solid transparent;
}


.header-wrapper{
  position: relative;
  background: url(/Portfolio-Project-React-Bootstrap/static/media/hero-img-1.161851fb.jpg) no-repeat;
  background-size: cover;
  background-position: cover;
  height: 100vh;

}

.button-container{
  display: flex;
  justify-content: center;
  align-items: center;

}

.main-info{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  top: -10rem;
  bottom: 0;
  right: 0;
  left: 0;

  font-family: "Roboto Condensed" sans-serif;
  z-index: 1;
}

.main-info h1{
  color: rgba(	255,140,0);
  color: var(--primary-orange);
  
}

/* **************TYPED TEXTED*********************** */
.typed-text{
  font-size: 2rem;
  color: #ffff;
  color: var(--primary-white);
  font-family: 'Times New Roman', Times, serif;
}

/* **************BTN MAIN OFFER*********************** */

.btn-main-offer{
  border: 1px solid rgba(128,0,0);
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem , 0.75rem, 0.75rem, 0.75rem;
  color: #ffff;
  color: var(--primary-white);
  margin: .8rem;
}

/* **************PARTICLES******************** */
.tsparticles-canvas-el{
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

/* ******************CARDS*********************** */

.cards-main-header{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  


}

.cards-main-header h3{

  border: 1px solid black;
  border-radius: .5rem;
  padding: 1rem;
  background-color: rgba(	255,140,0);
  background-color: var(--primary-orange);

}


.card-main-container{
  display: flex;
  justify-content: center;
  text-align: center;
  align-content: center;
  margin-top: 5rem;
  
}

.my-card{
  border: red 1px solid;
  height: 30rem;
  width: 20rem;
}


.my-card-btn{

  width: 8rem;
  min-height: 3.5rem;
  background-color: rgb(138, 134, 133);
  border-radius: .5rem;
}

/* *************SOCIAL MEDIA****************************** */
.social-media-header{
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-icon{
  margin: 1rem .4rem;
  border-radius: 5rem;

}




.section {
    padding: 100px 0;
    position: relative;
}
.gray-bg {
    background-color: #923737;
}
img {
    max-width: 100%;
}
img {
    vertical-align: middle;
    border-style: none;
}
/* About Me 
---------------------*/
.about-text h3 {
  font-size: 45px;
  font-weight: 700;
  margin: 0 0 6px;
}
@media (max-width: 767px) {
  .about-text h3 {
    font-size: 35px;
  }
}
.about-text h6 {
  font-weight: 600;
  margin-bottom: 15px;
}
@media (max-width: 767px) {
  .about-text h6 {
    font-size: 18px;
  }
}
.about-text p {
  font-size: 18px;
  max-width: 450px;
}
.about-text p mark {
  font-weight: 600;
  color: #20247b;
}

.about-list {
  padding-top: 10px;
}
.about-list .media {
  padding: 5px 0;
}
.about-list label {
  color: #20247b;
  font-weight: 600;
  width: 88px;
  margin: 0;
  position: relative;
}
.about-list label:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 11px;
  width: 1px;
  height: 12px;
  background: #20247b;
  -webkit-transform: rotate(15deg);
  transform: rotate(15deg);
  margin: auto;
  opacity: 0.5;
}
.about-list p {
  margin: 0;
  font-size: 15px;
}

@media (max-width: 991px) {
  .about-avatar {
    margin-top: 30px;
  }
}

.about-section .counter {
  padding: 22px 20px;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 30px rgba(31, 45, 61, 0.125);
}
.about-section .counter .count-data {
  margin-top: 10px;
  margin-bottom: 10px;
}
.about-section .counter .count {
  font-weight: 700;
  color: #20247b;
  margin: 0 0 5px;
}
.about-section .counter p {
  font-weight: 600;
  margin: 0;
}
mark {
    background-image: linear-gradient(rgba(252, 83, 86, 0.6), rgba(252, 83, 86, 0.6));
    background-size: 100% 3px;
    background-repeat: no-repeat;
    background-position: 0 bottom;
    background-color: transparent;
    padding: 0;
    color: currentColor;
}
.theme-color {
    color: #fc5356;
}
.dark-color {
    color: #20247b;
}
